import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, LoadingController, MenuController, AlertController, NavParams } from 'ionic-angular';

import { Item } from '../../models/item';
import { Items } from '../../providers';
import { User } from '../../providers';
import { Api } from '../../providers';
import { PARAMETERS } from '@angular/core/src/util/decorators';


@IonicPage()
@Component({
  selector: 'page-list-master',
  templateUrl: 'list-master.html'
})
export class ListMasterPage {
  currentItems: Item[];
  loaded: boolean;
  home: boolean = true;
  itemsshown: any;
  emptyevents: boolean = false;
  regular: boolean = false;
  special: boolean = false;
  senior: boolean = false;
  type: any = null;
  category: any = null;
  categories: any = [];
  hascategories: boolean = false;

  constructor(public navCtrl: NavController, public alertCtrl: AlertController, public items: Items, public api: Api,
     public menu: MenuController, public modalCtrl: ModalController, public user: User, public loadingCtrl: LoadingController, navParams: NavParams) {
    this.itemsshown = [];
    if (navParams.get('type')) {
      this.home = false;
      this.type = navParams.get('type');
      if (this.type === 1) {
        this.loadCategories();
      }
      this.loadItems();
    } else {
      this.loadInterests();
      this.home = true;
    }
  }



  ionViewCanEnter(){
    let seq = this.api.post('checktoken', []).share();
    seq.subscribe((res: any) => {
      // If the API returned a successful response, mark the user as logged in
      if (res.error !== true) {
        return true;
      } else {
        this.navCtrl.setRoot('LoginPage');
      } 
    }, err => {
      this.navCtrl.setRoot('LoginPage');
    });
  }

  ionViewDidEnter() {
    // the root left menu should be disabled on the tutorial page
    this.menu.enable(true);
  }

  /**
   * Prompt the user to add a new item. This shows our ItemCreatePage in a
   * modal and then adds the new item to our data source if the user created one.
   */
  addItem() {
    let addModal = this.modalCtrl.create('ItemCreatePage');
    addModal.onDidDismiss(item => {
      if (item) {
        this.items.add(item);
      }
    })
    addModal.present();
  }

  /**
   * Delete an item from the list of items.
   */
  deleteItem(item) {
    this.items.delete(item);
  }

  /**
   * Navigate to the detail page for this item.
   */
  openItem(item: Item) {
    this.navCtrl.push('ItemDetailPage', {
      item: item,
      'parentPage': this

    });
  }

  /**
   * Navigate to the detail page for this item.
   */
  openType(type: any) {
    console.log(type);
    this.navCtrl.push('ListMasterPage', {
      type: type
    });
  }

  loadItems() {
    let loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: `<img src="assets/svg/bars.svg" width="100%"/>`
    });
    loading.present();
    let params = [];
    if (this.type) {
      params["type"] = this.type;
    }
    if (this.category) {
      params["category"] = this.category;
    }
    let seq = this.items.query(params).share();
    seq.subscribe((res: any) => {
      this.currentItems = res;
      if (res.length == 0) {
        this.emptyevents = true;
      }
    }, err => {

    }, () => { loading.dismiss(); });
  }

  loadInterests() {
    let loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: `<img src="assets/svg/bars.svg" width="100%"/>`
    });
    loading.present();
    let seq = this.items.queryinterests().share();

    seq.subscribe((res: any) => {
      this.regular = res.regular;
      this.special = res.special;
      this.senior = res.senior;
    }, err => {
      loading.dismiss();
    }, () => {loading.dismiss();});
  }

  loadCategories() {
    let seq = this.items.querycategories().share();

    seq.subscribe((res: any) => {
      console.log(res);
      this.categories = res;
      if (res.length > 0) {
        this.hascategories = true;
      }
    }, err => {
    }, () => {});
  }

  isItemShown(id: never) {
    if (this.itemsshown.indexOf(id) !== -1) {
      return true;
    }
    return false;
  }
  toggleItem(id: never) {
    var position = this.itemsshown.indexOf(id);
    if (position !== -1) {
      this.itemsshown.splice(position, 1);
    } else {
      this.itemsshown.push(id);
    }
  }

  changeCategory() {
    this.loadItems();
  }
}



